
:root {
    --blue: #3A405E;
}
#wrap-configurator{
    border-radius: 60px 0px 0px 0px;
    background-color: var(--blue);
    color: #EBEAEA;
    display: inline-block; 
    padding-bottom: 78px;
    transition-duration: 0.3s;
    transition-timing-function: linear;
    position: fixed;
    height: calc(100vh - 40px);
    bottom: 0px;
    right: 0px;
    padding: 0px;
    width: 16%;
    opacity: 1;
    border-radius: 40px 0px 0px 0px;
    z-index: 2;
  
  }
  #bottom_configurtor{
    background-color: black;
    position: fixed;
    bottom: 0;
    right: 0;
    width: 16%;
    height: 30px;
    z-index: 3;
  }
  #head-configurator span, #head-configurator .a {
    color: #BCBCCB!important;
    fill: #BCBCCB;
    font-size: 0.8rem;
}
#head-configurator svg{
  height: 0.6rem;
}

  .button_collapse{
    color: #bdbec5;
    border-color:transparent;
    background-color: transparent;
   
  }
  #accordion .card-header *:hover{
    opacity: 0.6;
   
  }
  .button_collapse .btn-link:hover {
    color:#B1B1B1 ;
    text-decoration: none;
  }
  .button_collapse .btn-link{
    color:#B1B1B1 ;
    width: 100%;
    font-size:10px;
    text-decoration: none;
  }
  .button_collapse .btn-link :active  {
    text-decoration: none;
  }
  .button_collapse .card-header{
    border:1px solid #b1b1b1;
    border-radius: 0.5rem;
  }
  .arrow-collapse{
    font-size: 1.4rem;
  }